*{
padding:0;
margin:0;
box-sizing: border-box;
}
.navigation{
position: fixed;
width: 4%;
height: 100%;
border-left: 3px solid rgb(223, 226, 43);
  /* change made by insha */

background:  #2a2a72;
transition: 0.5s;
overflow: hidden;
z-index: 10;


}
.navbar-brand{
    font-family: Bebas Neue;
    letter-spacing: 4.5px;
    font-size: 30px !important;
}
.navigation::-webkit-scrollbar {
width: 16px;
}

.navigation::-webkit-scrollbar-track{
      border: 7px solid #232943;
      box-shadow: inset 0 0 2.5px 2px rgba(0,0,0,0.5);
    }
.navigation::-webkit-scrollbar-thumb{
      background: linear-gradient(
        45deg,
        #0d8cac,
        #dff516
        );
        border-radius: 3px;
    }
.navigation:hover,
.navigation.active{
width: 30%;
overflow: auto;

/* change made by insha */
}
.navigation ul{
position: absolute;
top: 50;
left: 0;
width: 100%;
}
.navigation ul li{
position: relative;
width: 100%;
list-style: none;
}
.navigation ul li:hover{
background: #FFDE00;
}
.navigation ul li a{
position: relative;
display: block;
width: 100%;
display: flex;
text-decoration: none;
color: #ffffff;
}
.navigation ul li a:hover{
color: black;
}


.navigation ul li a .icon{
position: relative;
display: block;
min-width: 60px;
height: 60px;
line-height: 60px;
text-align: center;
}
.navigation ul li a .icon .fa{
font-size: 24px;
}
.navigation ul li a .title{
position: relative;
display: block;
padding: 0 10px;
height: 60px;
line-height: 60px;
text-align: start;
white-space: nowrap;
}
.toggle{
position: absolute;
top: 0;
right: 0;
width: 60px;
height: 60px;
cursor: pointer;
background: #330748;
}
.toggle:active{
background: #2a2a72;
  /* change made by insha */

}
.toggle::before{
content: '\f0c9';
position: absolute;
width: 100%;
height: 100%;
line-height: 60px;
text-align: center;
font-size: 24px;
color: red;
}
.toggle:active::before{
content: '\f00d';
}
.btn btn-secondary dropdown-toggle{
padding: 50%;
}
@media (max-width: 768px) {
.navigation{
  left:-60px;
}

.navigation.active{
  left:0px;
  width:100%;
}
}
.logo {
          border-radius: 48%;
          height: 40px;

   }


