 .navbar-nav li a{
   text-transform: uppercase;
   font-size: 18px;
   position: relative;
 }
 .navbar-nav li a:before{
   content:'';
   position: absolute;
   top:100%;
   bottom: 50%;
   width: 0%;
   height: 3%;
   background: blue;
   transition: all 0.3s linear;
 }
 .navbar-nav li a:hover:before{
   width: 80%;
 }
 .navbar-nav li a:hover{
   color: rgb(255, 230, 0) !important;
 }
 
 .department-card-design--enter{
  opacity: 0;
}

.department-card-design--enter-active{
  opacity: 1;
  transition:all 500ms;
}

.department-card-design--enter-done{
  opacity: 1;
}

.department-card-design--exit{
  opacity: 1;
}

.department-card-design--exit-active{
  opacity: 0;
  transition: opacity 500ms;
}

.department-card-design--exit-done{
  opacity:0;
}
